@import "../../style/colors.scss";
@import "../../style/metrics.scss";

.contact-container {
    width: inherit;
    height: 1080px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.row {
    height: 100%;
}

.box {
    display: flex;
    background-color: $primary-light;
    width: 100%;
    height: 650px;
    border-radius: $baseRadius;
    align-items: center;
    justify-content: center;
    padding: 40px;
    padding-left: 100px;
    padding-right: 100px;
    border-radius: 20px;
}

.contact-title-container {
    text-align: center;
    //margin-bottom: $baseMargin + 30;
}

.page-title.contact-me {
    font-size: 32px;
}

.contact-title {
    color: $heading;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 23px;
}

.contact-icons {
    font-size: 25px;
    padding: 10px;
}

.input-container {
    display: flex;
    align-items: center;
    border: 1px solid $secondary;
    //width: inherit;
    height: fit-content;
    border-radius: $baseRadius;
    padding: 5px 10px;
    overflow: hidden;
}

.text-area {
    align-items: flex-start;
}

.input.form-control {
    width: 100%;
    overflow-y: scroll;
    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    background-color: $primary-light;
    border-width: 0px;
    outline: none;
    resize: none;
}

.input.form-control:invalid {
    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    background-color: $primary-light;
    box-shadow: none;
    outline: none;
    resize: none;
}

.input.form-control::placeholder {
    color: $heading;
    opacity: 0.8;
}

.input.form-control::selection {
    color: white;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    background-color: $primary-light;
    border: 0px;
    outline: none;
    resize: none;
}

.input.input.form-control:focus {
    color: $secondary;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    background-color: $primary-light;
    border: 1px;
    //outline: none;
    resize: none;
    box-shadow: none;
}

.input.form-control::-webkit-scrollbar {
    background: red;
}

textarea.input.form-control::-webkit-scrollbar-track {
    background-color: $primary-light;
}

textarea.input.form-control::-webkit-scrollbar-thumb {
    background-color: $secondary;
    border-radius: 20px;
    border: 3px solid $primary-light;
}

body::-webkit-scrollbar {
    background: red;
}

body::-webkit-scrollbar-track {
    background-color: $primary-light;
}

body::-webkit-scrollbar-thumb {
    background-color: $secondary;
    border-radius: 20px;
    border: 3px solid $primary-light;
}

.input .message {
    height: 300px;
}

.thankyouImg {
    width: 300px;
    margin-top: 30px;
    margin-bottom: 50px;
}

#submit-but {
    color: $primary;
    padding: 10px 60px;
    background-color: $secondary;
    border: 0px;
    border-radius: $baseRadius;
    outline: none;
    box-shadow: none;
}

@media only screen and (max-width: $mobileScreen) {
    .box {
        padding: 20px;
        padding-left: 0px;
        padding-right: 0px;
    }

    .input.form-control {
        width: 100%;
        overflow-y: scroll;
        padding: 10px 0px;
    }

    .thankyouImg {
        width: 200px;
        margin-top: 30px;
        margin-bottom: 50px;
    }
}
